<template>
  <div id="chargeBox">
    <div class="timeAround">
      <p>日期范围</p>&ensp;&ensp;&ensp;
      <span
        :class=" longTime == 7 ? 'seven' : ''"
        @click="selectTime(7)"
      >近7天</span>
      <span
        :class=" longTime == 30 ? 'seven' : ''"
        @click="selectTime(30)"
      >近30天</span>
      <span
        :class=" longTime == 90 ? 'seven' : ''"
        @click="selectTime(90)"
      >近90天</span>
    </div>
    <div class="details">
      <p>使用情况</p>
      <table class="tableBox">
        <tr>
          <th>日期</th>
          <th>最后电量</th>
        </tr>
        <tr
          v-for="(item,i) in info"
          :key="i"
        >
          <td>{{ item.Date }}</td>
          <td>{{ item.Allpower }}</td>
        </tr>
      </table>
    </div>
  </div>
</template>
<script>
import { GetBusinessElecBillReportByorderroom } from '@/api/api_business_hotel_and_scale';
import dayjs from 'dayjs';
export default {
    data() {
        return {
            currentInfo: [],
            longTime: 7,
            formData: {
                endDate: dayjs().format('YYYY-MM-DD'),
                startDate: dayjs()
                    .subtract(7, 'day')
                    .format('YYYY-MM-DD')
            },
            info: []
        };
    },
    created() {
        this.$store.commit('setHeader', '用电统计');
        this.orderRoomId = this.$route.query.orderRoomId;
        this.getCurrentInfo();
    },

    methods: {
        getCurrentInfo() {
            GetBusinessElecBillReportByorderroom(this.orderRoomId, this.formData).done((res) => {
                this.info = Object.entries(res.data).map((item) => {
                    let obj = {
                        Date: item[0],
                        Allpower: item[1]
                    };
                    return obj;
                });
            });
        },

        //切换时间
        selectTime(time) {
            this.longTime = time;
            this.formData.startDate = dayjs()
                .subtract(time, 'day')
                .format('YYYY-MM-DD');
            this.getCurrentInfo();
        }
    }
};
</script>
<style scoped>
#chargeBox {
    font-size: 0.8rem;
    color: #b2b2b2;
}
#chargeBox > .timeAround {
    background: #fff;
    display: flex;
    margin-bottom: 20px;
    padding: 15px;
    font-size: 0.7rem;
    line-height: 24px;
}
#chargeBox > .timeAround > p {
    width: 20%;
}
#chargeBox > .timeAround > span {
    border: 1px solid #cbcbcb;
    width: 20%;
    margin-right: 4%;
    border-radius: 20px;
}
#chargeBox .timeAround .seven {
    border: 1px solid #ff7d41;
    color: #ff7d41;
}
#chargeBox > .details {
    background: #fff;
    position: fixed;
    bottom: 0;
    left: 0;
    top: 116px;
    right: 0;
    overflow-y: scroll;
}
#chargeBox > .details > p {
    color: #000;
    text-align: left;
    margin: 10px;
}
#chargeBox > .details > .tableBox {
    margin: 10px;
    width: 94%;
    border: solid #cbcbcb;
    border-width: 1px 0px 0px 1px;
    font-size: 0.8rem;
    border-collapse: collapse;
    border-spacing: 0;
}
#chargeBox > .details > .tableBox tr {
    height: 30px;
    line-height: 30px;
    color: #000;
}
#chargeBox > .details > .tableBox tr:nth-child(odd) {
    background-color: #fff7f3;
}
#chargeBox > .details > .tableBox tr:nth-child(even) {
    background-color: #fff;
}
#chargeBox > .details > .tableBox > tr:nth-child(1) {
    background-color: #ffede5;
}
#chargeBox > .details > .tableBox tr th,
#chargeBox > .details > .tableBox tr td {
    padding: 3px 5px;
    text-align: center;
    border: solid #cbcbcb;
    border-width: 0px 1px 1px 0px;
}
#chargeBox > .details > .tableBox tr th {
    text-align: center;
    color: #b2b2b2;
}
</style>



